import React from 'react';
import {
  FluentProvider,
  webLightTheme,
  Card,
  Button,
  Input,
  Label,
  Dropdown,
  Option,
  Text,
  Title3,
  Body1,
  Caption1,
  tokens
} from '@fluentui/react-components';
import { Add24Regular, Calculator24Regular, Dismiss24Regular } from '@fluentui/react-icons';
import './App.css';

// Type definitions
interface RateStructure {
  greenFreight: number;
}

interface RipeRateStructure extends RateStructure {
  ripening: number;
  ripeFreight: number;
}

interface LocationRates {
  green: RateStructure;
  ripe: RipeRateStructure;
}

interface RatesState {
  [key: string]: LocationRates;
}

interface AllPrices {
  [key: string]: {
    green: string;
    ripe: string;
  };
}

function App() {
  const [rates, setRates] = React.useState<RatesState>({
    brisbane: {
      green: { greenFreight: 0 },
      ripe: { greenFreight: 0, ripening: 8.50, ripeFreight: 0 }
    },
    melbourne: {
      green: { greenFreight: 12.75 },
      ripe: { greenFreight: 12.75, ripening: 8.50, ripeFreight: 14.25 }
    },
    tasmania: {
      green: { greenFreight: 18.00 },
      ripe: { greenFreight: 18.00, ripening: 8.50, ripeFreight: 20.00 }
    }
  });

  const [basePrice, setBasePrice] = React.useState<string>('');
  const [destination, setDestination] = React.useState<string>('brisbane');
  const [isRipe, setIsRipe] = React.useState<'green' | 'ripe'>('green');
  const [showAllPrices, setShowAllPrices] = React.useState<boolean>(false);
  const [newLocation, setNewLocation] = React.useState<string>('');
  const [allPrices, setAllPrices] = React.useState<AllPrices>({});

  const calculatePrice = (loc: string, ripeness: 'green' | 'ripe'): string => {
    if (!basePrice) return '0.00';
    const pricePerCarton = parseFloat(basePrice);
    const destinationRates = rates[loc];
    
    if (ripeness === 'green') {
      return (pricePerCarton + destinationRates.green.greenFreight).toFixed(2);
    } else {
      const ripeRates = destinationRates.ripe;
      return (pricePerCarton + ripeRates.greenFreight + ripeRates.ripening + ripeRates.ripeFreight).toFixed(2);
    }
  };

  const handleAddLocation = (): void => {
    if (newLocation.trim() && !rates[newLocation.toLowerCase()]) {
      const locationKey = newLocation.toLowerCase();
      const updatedRates: RatesState = {
        ...rates,
        [locationKey]: {
          green: { greenFreight: 0 },
          ripe: { greenFreight: 0, ripening: 8.50, ripeFreight: 0 }
        }
      };
      setRates(updatedRates);
      setNewLocation('');
    }
  };

  const handleRemoveLocation = (location: string): void => {
    const updatedRates = { ...rates };
    delete updatedRates[location];
    setRates(updatedRates);
    if (location === destination) {
      setDestination(Object.keys(updatedRates)[0]);
    }
  };

  const handleRateChange = (location: string, type: 'green' | 'ripe', subtype: keyof RateStructure | keyof RipeRateStructure, value: string): void => {
    setRates(prev => ({
      ...prev,
      [location]: {
        ...prev[location],
        [type]: {
          ...prev[location][type],
          [subtype]: parseFloat(value) || 0
        }
      }
    }));
  };

  React.useEffect(() => {
    const newPrices: AllPrices = {};
    Object.keys(rates).forEach(loc => {
      newPrices[loc] = {
        green: calculatePrice(loc, 'green'),
        ripe: calculatePrice(loc, 'ripe')
      };
    });
    setAllPrices(newPrices);
  }, [basePrice, rates]);

  return (
    <FluentProvider theme={webLightTheme}>
      <div className="container">
        <Card className="calculator-card">
          <Title3>Banana Pricing Calculator (15kg Carton)</Title3>
          
          <div className="form-group">
            <Label>Base Price ($ per 15kg carton in Brisbane - Green)</Label>
            <Input
              type="number"
              value={basePrice}
              onChange={(e, data) => setBasePrice(data.value)}
            />
          </div>

          <div className="form-group">
            <Label>Destination</Label>
            <Dropdown
              value={destination}
              onOptionSelect={(_, data) => data.optionValue && setDestination(data.optionValue)}
            >
              {Object.keys(rates).map(loc => (
                <Option key={loc} value={loc}>
                  {loc.charAt(0).toUpperCase() + loc.slice(1)}
                </Option>
              ))}
            </Dropdown>
          </div>

          <div className="form-group">
            <Label>Ripeness State</Label>
            <Dropdown
              value={isRipe}
              onOptionSelect={(_, data) => data.optionValue && setIsRipe(data.optionValue as 'green' | 'ripe')}
            >
              <Option value="green">Green</Option>
              <Option value="ripe">Ripe</Option>
            </Dropdown>
          </div>

          <div className="price-display">
            <Label>Calculated Price:</Label>
            <Text size={800}>${calculatePrice(destination, isRipe)} per carton</Text>
          </div>

          <Button
            icon={<Calculator24Regular />}
            onClick={() => setShowAllPrices(!showAllPrices)}
          >
            {showAllPrices ? 'Hide All Prices' : 'Calculate All Prices'}
          </Button>

          {showAllPrices && (
            <div className="all-prices">
              {Object.keys(rates).map(loc => (
                <Card key={loc} className="price-card">
                  <Body1 as="h3">{loc.charAt(0).toUpperCase() + loc.slice(1)}</Body1>
                  <div className="price-grid">
                    <div className="price-block green">
                      <Body1>Green: ${calculatePrice(loc, 'green')}</Body1>
                      <Caption1>+ ${rates[loc].green.greenFreight.toFixed(2)} freight</Caption1>
                    </div>
                    <div className="price-block ripe">
                      <Body1>Ripe: ${calculatePrice(loc, 'ripe')}</Body1>
                      <Caption1>
                        + ${rates[loc].ripe.greenFreight.toFixed(2)} green freight<br />
                        + ${rates[loc].ripe.ripening.toFixed(2)} ripening<br />
                        + ${rates[loc].ripe.ripeFreight.toFixed(2)} ripe freight
                      </Caption1>
                    </div>
                  </div>
                </Card>
              ))}
            </div>
          )}
        </Card>

        <Card className="rates-card">
          <Title3>Manage Rates</Title3>
          
          <div className="add-location">
            <Input
              placeholder="Enter new location name"
              value={newLocation}
              onChange={(e, data) => setNewLocation(data.value)}
            />
            <Button icon={<Add24Regular />} onClick={handleAddLocation}>
              Add
            </Button>
          </div>

          {Object.entries(rates).map(([location, locationRates]) => (
            <Card key={location} className="location-card">
              <div className="location-header">
                <Body1>{location.charAt(0).toUpperCase() + location.slice(1)}</Body1>
                <Button
                  icon={<Dismiss24Regular />}
                  appearance="subtle"
                  onClick={() => handleRemoveLocation(location)}
                />
              </div>

              <div className="rate-section green">
                <Body1>Green Banana Rates</Body1>
                <div className="form-group">
                  <Label>Green Freight Rate ($ per carton)</Label>
                  <Input
                    type="number"
                    value={locationRates.green.greenFreight.toString()}
                    onChange={(e, data) => handleRateChange(location, 'green', 'greenFreight', data.value)}
                  />
                </div>
              </div>

              <div className="rate-section ripe">
                <Body1>Ripe Banana Rates</Body1>
                <div className="form-group">
                  <Label>Green Freight Rate ($ per carton)</Label>
                  <Input
                    type="number"
                    value={locationRates.ripe.greenFreight.toString()}
                    onChange={(e, data) => handleRateChange(location, 'ripe', 'greenFreight', data.value)}
                  />
                </div>
                <div className="form-group">
                  <Label>Ripening Rate ($ per carton)</Label>
                  <Input
                    type="number"
                    value={locationRates.ripe.ripening.toString()}
                    onChange={(e, data) => handleRateChange(location, 'ripe', 'ripening', data.value)}
                  />
                </div>
                <div className="form-group">
                  <Label>Ripe Freight Rate ($ per carton)</Label>
                  <Input
                    type="number"
                    value={locationRates.ripe.ripeFreight.toString()}
                    onChange={(e, data) => handleRateChange(location, 'ripe', 'ripeFreight', data.value)}
                  />
                </div>
              </div>
            </Card>
          ))}
        </Card>
      </div>
    </FluentProvider>
  );
}

export default App;